/* Rich Text Editor Default Styles */
.rdw-editor-main,
.DraftEditor-root,
.public-DraftEditor-content,
.public-DraftEditor-content div,
.public-DraftEditor-content span,
.public-DraftEditor-content p {
  font-size: 1rem !important;
  font-family: 'Merriweather Sans', -apple-system, BlinkMacSystemFont, sans-serif !important;
  font-weight: 400;
  line-height: 1.75;
}

.rdw-editor-main {
  min-height: 300px;
  padding: 12px;
}

.rdw-editor-toolbar {
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 0;
}

/* Ensure link button is visible and functional */
.rdw-link-wrapper,
.rdw-image-wrapper {
  display: inline-flex !important;
}

.rdw-option-wrapper {
  border: 1px solid #e5e7eb;
  min-width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.rdw-option-wrapper:hover {
  box-shadow: 1px 1px 0 #bfbdbd;
  background-color: #f3f4f6;
}

.rdw-option-active {
  background-color: #e5e7eb;
}


/* Force Merriweather Sans on all prose content - Override any inline styles */
.prose,
.prose *,
.prose p,
.prose div,
.prose span,
.prose h1,
.prose h2,
.prose h3,
.prose h4,
.prose h5,
.prose h6,
.prose li,
.prose a,
.prose strong,
.prose em,
.prose blockquote {
  font-family: 'Merriweather Sans', -apple-system, BlinkMacSystemFont, sans-serif !important;
  background-color: transparent !important;
  background: none !important;
}

/* Default font weight and size for article main content */
.prose p,
.prose div,
.prose span,
.prose li {
  font-size: 1rem !important;
  font-weight: 400;
}

/* Override any inline font-size styles */
.prose p[style*="font-size"],
.prose div[style*="font-size"],
.prose span[style*="font-size"] {
  font-size: 1rem !important;
}

/* Good paragraph spacing and line height for readability */
.prose p {
  margin-bottom: 1.25em;
  line-height: 1.75;
}

.prose p:last-child {
  margin-bottom: 0;
}

/* Line height for better readability */
.prose div,
.prose span {
  line-height: 1.75;
}

/* Override ProximaNova font specifically */
[style*="ProximaNova"],
[style*="Proxima Nova"],
[style*="font-family"] {
  font-family: 'Merriweather Sans', -apple-system, BlinkMacSystemFont, sans-serif !important;
}

/* Override inline font-size to always use 1rem for body text */
.prose [style*="font-size: 20px"],
.prose [style*="font-size: 18px"],
.prose [style*="font-size: 22px"],
.prose [style*="font-size: 24px"] {
  font-size: 1rem !important;
}

/* Allow bold text from rich text editor */
.prose strong,
.prose b,
.prose [style*="font-weight: 700"],
.prose [style*="font-weight: bold"] {
  font-weight: 700 !important;
}
